@import 'scss/variables/index';

@media only screen and (min-width: $phone) {
  .pd-header {
    &__v2 {
      font-weight: bold;
      padding: 20px;

      .logo {
        width: 30px;
      }
      .company {
        font-size: 16px;
        margin-left: 15px;
      }
    }
  }
}

@media only screen and (min-width: $tablet) {
  .pd-header {
    &__v2 {
      margin: 0 -70px;
      padding: 20px 70px;

      .logo {
        width: 36px;
      }
      .company {
        font-size: $pd-font-size-md;
        margin-left: 20px;
      }
    }
  }
}
